import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCloudDownloadAlt, faEye, faFilePdf, faFileAlt } from '@fortawesome/free-solid-svg-icons';
import pdf from '../Files/Andrew_Liu_2.pdf'

function Resume() {
    return (<div className="" data-aos="fade-right" >
        <h1 className="font-semibold text-3xl mb" >

        <FontAwesomeIcon className="mr-3"
                        icon={faFileAlt}
                    />Download My Resume
</h1>
        <div className="p-3">
            <a href={pdf} download={pdf}>
                <button className="bg-transparent hover:bg-teal-500 text-teal-dark font-semibold hover:text-white py-2 px-4 border border-teal-500 hover:border-transparent rounded mr-2 focus:outline-none">
                    <FontAwesomeIcon
                        icon={faCloudDownloadAlt}
                    />  Download</button></a>
            <a href={pdf}>
                <button className="bg-transparent hover:bg-teal-500 text-teal-dark font-semibold hover:text-white py-2 px-4 border border-teal-500 hover:border-transparent rounded mr-2 focus:outline-none">
                    <FontAwesomeIcon
                        icon={faEye}
                    /> View</button>
            </a>
        </div>
    </div>)
}

export default Resume