import React, {useState} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
function Spring2020() {
    return (
<div> 
        <div className="flex items-center mb-1 ">
            <div className="bg-teal-500 rounded-full h-8 w-8"></div>
            <div className="flex-1 ml-4 font-semibold">January 2020 - Master's Spring Semester</div>
        </div>
        <div className="ml-12">
            <span className="p-1 flex font-medium"> Intelligent Systems: </span>
            <span className="p-1 flex">8-puzzle problem - A* search (Java)
  <a className="px-1 hover:text-teal-500"
                    href="https://github.com/aliu47/ITCS-6150-Project-1-Solving-8-puzzle-using-A-search-algorithm">
                    <FontAwesomeIcon
                        icon={faGithub}
                    /></a></span>
            <span className="p-1 flex">N Queen problem - Hill Climbing Search (Java)
    <a className="px-1 hover:text-teal-500"
                    href="https://github.com/aliu47/ITCS-6150-Project-2-Solving-N-queens-using-Hill-Climbing">
                    <FontAwesomeIcon
                        icon={faGithub}
                    /></a>
            </span>
            <span className="p-1 flex font-medium">
                Computer Communication and Networks:</span>
            <span className="p-1 flex">Chatroom application - Sockets (Java)
    <a className="px-1 hover:text-teal-500"
                    href="https://github.com/aliu47/ITCS-6166-Project-1-Socket-Programming-Multi-user-Chat">
                    <FontAwesomeIcon
                        icon={faGithub}
                    /></a>
            </span>
            <span className="p-1 flex font-medium">
                Teaching Assistant:</span>
                <span className="p-1 flex">Secure programming and Pentration Testing</span>
                <span className="p-1 flex"></span>
            {/* For my last semester the two significant courses I took were intelligent systems and Computer communication and networks. In the intelligent system course
    I solved the N Queen problem by developing a Hill Climbing search algorithm and I solved the 8-puzzle problem using A* search. For my communication and networks course
    I developed a chatroom application in java using Sockets. unfortunately the lessons online due to COVID-19 were not as engaging as the in person sessions making it more difficult to learn.
    However, I was still able to complete my Master's degree. */}

        </div>
    </div>
    )
}

export default Spring2020